import { useState } from 'react';
import { validateForm } from '../utils/helpers';

const CreatePost = ({ onPostCreated, onCancel }) => {
  const [formData, setFormData] = useState({
    title: '',
    content: '',
    categories: []
  });
  const [errors, setErrors] = useState({});
  const [loading, setLoading] = useState(false);

  const categories = [
    "Tech Support",
    "General Discussion",
    "Tutorials", 
    "Gaming",
    "Hobbies & Interests",
    "Job Listings",
    "Announcements"
  ];

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
    
    // Clear error when user starts typing
    if (errors[name]) {
      setErrors(prev => ({
        ...prev,
        [name]: ''
      }));
    }
  };

  const handleCategoryChange = (e) => {
    const { value, checked } = e.target;
    setFormData(prev => ({
      ...prev,
      categories: checked
        ? [...prev.categories, value]
        : prev.categories.filter(cat => cat !== value)
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    const validation = validateForm(formData);
    if (!validation.isValid) {
      setErrors(validation.errors);
      return;
    }

    setLoading(true);
    setErrors({});

    try {
      const submitData = new FormData();
      submitData.append('title', formData.title);
      submitData.append('content', formData.content);
      
      // Add categories
      formData.categories.forEach(category => {
        submitData.append('categories', category);
      });

      const result = await onPostCreated(submitData);
      
      if (result.success) {
        // Reset form
        setFormData({
          title: '',
          content: '',
          categories: []
        });
        onCancel(); // Close the form
      } else {
        setErrors({ general: result.error || 'Failed to create post' });
      }
    } catch (error) {
      setErrors({ general: 'Failed to create post' });
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="form-container">
      <form name="creatpost" onSubmit={handleSubmit}>
        <div className="form-group">
          <div>
            <span className="material-icons" id="close" onClick={onCancel}>
              close
            </span>
            <label>Post Title</label>
            <input
              type="text"
              name="title"
              className="form-control"
              placeholder="Enter post title"
              value={formData.title}
              onChange={handleInputChange}
              required
            />
            {errors.title && <span className="error">{errors.title}</span>}
          </div>
        </div>

        <div className="form-group">
          <label>Post Content</label>
          <textarea
            className="form-control"
            name="content"
            rows="5"
            placeholder="Write your post content"
            value={formData.content}
            onChange={handleInputChange}
            required
          />
          {errors.content && <span className="error">{errors.content}</span>}
        </div>

        <div className="form-group">
          <label>Select Categories</label>
          <div className="category-grid">
            {categories.map((category) => (
              <label key={category} className="category-checkbox">
                <input
                  type="checkbox"
                  name="categories"
                  value={category}
                  checked={formData.categories.includes(category)}
                  onChange={handleCategoryChange}
                />
                {category}
              </label>
            ))}
          </div>
        </div>

        {errors.general && (
          <p id="error-message-creatpost" className="error">{errors.general}</p>
        )}

        <button type="submit" className="submit-btn" disabled={loading}>
          {loading ? 'Submitting...' : 'Submit Post'}
        </button>
      </form>
    </div>
  );
};

export default CreatePost;
