import { useState, useEffect, useCallback } from 'react';
import { apiService } from '../services/api';
import { deleteCookie } from '../utils/helpers';

export const useAuth = () => {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    checkAuthStatus();
  }, []);

  const checkAuthStatus = async () => {
    try {
      setLoading(true);
      console.log('Checking auth status...');
      const response = await apiService.checkUserStatus();
      console.log('Auth status response:', response);
      
      if (response.authenticated) {
        console.log('User authenticated:', response.user);
        setUser(response.user);
      } else {
        console.log('User not authenticated');
        setUser(null);
      }
    } catch (err) {
      console.error('Auth check error:', err);
      setError('Failed to check authentication status');
      setUser(null);
    } finally {
      setLoading(false);
    }
  };

  const login = async (email, password) => {
    try {
      setError(null);
      const response = await apiService.login(email, password);
      if (response.success) {
        await checkAuthStatus();
        return { success: true };
      } else {
        setError(response.error || 'Login failed');
        return { success: false, error: response.error };
      }
    } catch (err) {
      const errorMsg = 'Login failed';
      setError(errorMsg);
      return { success: false, error: errorMsg };
    }
  };

  const logout = async () => {
    try {
      await apiService.logout();
      deleteCookie('SessionToken');
      setUser(null);
      return { success: true };
    } catch (err) {
      setError('Logout failed');
      return { success: false };
    }
  };

  const register = async (userData) => {
    try {
      setError(null);
      const response = await apiService.register(userData);
      if (response.success) {
        return { success: true };
      } else {
        setError(response.error || 'Registration failed');
        return { success: false, error: response.error };
      }
    } catch (err) {
      const errorMsg = 'Registration failed';
      setError(errorMsg);
      return { success: false, error: errorMsg };
    }
  };

  return {
    user,
    loading,
    error,
    login,
    logout,
    register,
    checkAuthStatus
  };
};

export const usePosts = () => {
  const [posts, setPosts] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [hasMore, setHasMore] = useState(true);

  const fetchPosts = useCallback(async (category = null, reset = false) => {
    try {
      setLoading(true);
      setError(null);
      
      let response;
      if (category && category !== 'all') {
        response = await apiService.getPostsByCategory(category, reset);
      } else {
        response = await apiService.getPosts(reset);
      }

      if (response.tocken === false) {
        // Token expired, handle authentication
        throw new Error('Authentication required');
      }

      if (reset) {
        setPosts(response);
      } else {
        setPosts(prev => [...prev, ...response]);
      }

      setHasMore(response.length === 10);
    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  }, []);

  const createPost = async (formData) => {
    try {
      setError(null);
      const response = await apiService.createPost(formData);
      
      if (response.status) {
        // Refresh posts after creating
        await fetchPosts(null, true);
        return { success: true };
      } else {
        if (response.tocken === false) {
          throw new Error('Authentication required');
        }
        throw new Error(response.error || 'Failed to create post');
      }
    } catch (err) {
      setError(err.message);
      return { success: false, error: err.message };
    }
  };

  return {
    posts,
    loading,
    error,
    hasMore,
    fetchPosts,
    createPost,
    setPosts
  };
};

export const useComments = () => {
  const [comments, setComments] = useState({});
  const [loading, setLoading] = useState({});
  const [error, setError] = useState(null);

  const fetchComments = async (postId) => {
    try {
      setLoading(prev => ({ ...prev, [postId]: true }));
      setError(null);
      
      const response = await apiService.getComments(postId);
      
      if (response.token === false) {
        throw new Error('Authentication required');
      }

      setComments(prev => ({ ...prev, [postId]: response }));
    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(prev => ({ ...prev, [postId]: false }));
    }
  };

  const sendComment = async (content, postId) => {
    try {
      setError(null);
      const response = await apiService.sendComment(content, postId);
      
      if (response.status) {
        // Refresh comments for this post
        await fetchComments(postId);
        return { success: true };
      } else {
        if (response.tocken === false) {
          throw new Error('Authentication required');
        }
        throw new Error(response.error || 'Failed to send comment');
      }
    } catch (err) {
      setError(err.message);
      return { success: false, error: err.message };
    }
  };

  const toggleComments = (postId) => {
    if (comments[postId]) {
      // Hide comments
      setComments(prev => {
        const newState = { ...prev };
        delete newState[postId];
        return newState;
      });
    } else {
      // Fetch and show comments
      fetchComments(postId);
    }
  };

  return {
    comments,
    loading,
    error,
    fetchComments,
    sendComment,
    toggleComments
  };
};

export const useReactions = () => {
  const [loading, setLoading] = useState({});
  const [error, setError] = useState(null);

  const toggleReaction = async (contentType, contentId, reactionType) => {
    try {
      setLoading(prev => ({ ...prev, [`${contentType}-${contentId}`]: true }));
      setError(null);
      
      const response = await apiService.toggleReaction(contentType, contentId, reactionType);
      
      if (response.tocken === false) {
        throw new Error('Authentication required');
      }

      return { success: true, data: response };
    } catch (err) {
      setError(err.message);
      return { success: false, error: err.message };
    } finally {
      setLoading(prev => ({ ...prev, [`${contentType}-${contentId}`]: false }));
    }
  };

  return {
    loading,
    error,
    toggleReaction
  };
};
