

'use client';

import { useState, useEffect, useCallback } from 'react';
import { useRouter } from 'next/navigation';
import { apiService } from '../../services/api';
import { debounce } from '../../utils/helpers';
import Post from '../../components/Post';
import CreatePost from '../../components/CreatePost';
import Sidebar from '../../components/Sidebar';
import ChatWebSocket from '../../components/ChatWebSocket';

export default function Home() {
  const router = useRouter();
  const [user, setUser] = useState(null);
  const [authLoading, setAuthLoading] = useState(true);
  const [posts, setPosts] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [hasMore, setHasMore] = useState(true);
  const [showCreatePost, setShowCreatePost] = useState(false);
  const [selectedCategory, setSelectedCategory] = useState('all');

  // Check authentication on component mount
  useEffect(() => {
    const checkAuth = async (retryCount = 0) => {
      try {
        console.log('Checking authentication... attempt:', retryCount + 1);
        const result = await apiService.checkUserStatus();
        console.log('Auth result:', result);
        
        if (result.authenticated && result.user) {
          setUser(result.user);
          console.log('User authenticated:', result.user);
          setAuthLoading(false);
        } else {
          // If this is the first check and authentication failed, retry once after a short delay
          if (retryCount === 0) {
            console.log('Authentication failed, retrying in 500ms...');
            setTimeout(() => checkAuth(1), 500);
          } else {
            console.log('User not authenticated after retry, redirecting to login');
            setAuthLoading(false);
            router.push('/');
          }
        }
      } catch (error) {
        console.error('Auth check failed:', error);
        // On error, also retry once if this is the first attempt
        if (retryCount === 0) {
          console.log('Auth check error, retrying in 500ms...');
          setTimeout(() => checkAuth(1), 500);
        } else {
          setAuthLoading(false);
          router.push('/');
        }
      }
    };

    checkAuth();
  }, [router]);

  // Fetch posts when user is available
  useEffect(() => {
    if (user && !authLoading) {
      fetchPosts(null, true);
    }
  }, [user, authLoading]);

  const fetchPosts = async (category = null, reset = false) => {
    try {
      setLoading(true);
      setError(null);
      
      let response;
      if (category && category !== 'all') {
        response = await apiService.getPostsByCategory(category, reset);
      } else {
        response = await apiService.getPosts(reset);
      }
      
      if (reset) {
        setPosts(response || []);
      } else {
        setPosts(prev => [...prev, ...(response || [])]);
      }
      
      setHasMore(response && response.length === 10);
    } catch (err) {
      setError('Failed to fetch posts');
      console.error('Fetch posts error:', err);
    } finally {
      setLoading(false);
    }
  };

  const createPost = async (formData) => {
    try {
      const response = await apiService.createPost(formData);
      if (response.status) {
        await fetchPosts(null, true); // Refresh posts
        return { success: true };
      } else {
        return { success: false, error: response.error };
      }
    } catch (error) {
      return { success: false, error: 'Failed to create post' };
    }
  };

  const logout = async () => {
    try {
      const response = await apiService.logout();
      if (response.status) {
        setUser(null);
        router.push('/');
        return { success: true };
      }
    } catch (error) {
      console.error('Logout error:', error);
    }
    return { success: false };
  };

  const handleScroll = useCallback(
    debounce(() => {
      if (!hasMore || loading) return;
      
      const scrollableHeight = document.body.offsetHeight;
      const scrolled = window.innerHeight + window.scrollY;
      
      if (scrollableHeight - scrolled < 500) {
        fetchPosts(selectedCategory === 'all' ? null : selectedCategory, false);
      }
    }, 300),
    [hasMore, loading, selectedCategory, fetchPosts]
  );

  useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, [handleScroll]);

  const handleCategoryChange = (category) => {
    setSelectedCategory(category);
    fetchPosts(category === 'all' ? null : category, true);
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  const handleCreatePost = () => {
    setShowCreatePost(true);
  };

  const handlePostCreated = async (formData) => {
    const result = await createPost(formData);
    return result;
  };

  const handlePostUpdate = (postId, reactionData) => {
    setPosts(prevPosts => 
      prevPosts.map(post => 
        post.ID === postId 
          ? { 
              ...post, 
              Like: reactionData.like || post.Like,
              DisLike: reactionData.dislike || post.DisLike,
              Have: reactionData.content_type || post.Have
            }
          : post
      )
    );
  };

  const handleLogout = async () => {
    const result = await logout();
    if (result.success) {
      // Clear any local state and redirect
      router.push('/');
    }
  };

  if (authLoading) {
    return <div className="loading">Loading...</div>;
  }

  if (!user) {
    return null; // Will redirect in useEffect
  }

  return (
    <>
      <header className="header">
        <div className="logo">FORUM</div>
        <button id="logout" onClick={handleLogout}>
          logout
        </button>
      </header>

      <div className="container">
        <Sidebar 
          username={user.nickname || user.username} 
          onCategoryChange={handleCategoryChange}
        />

        <main className="main-content" id="main-content">
          {!showCreatePost && (
            <div className="create-post">
              <button onClick={handleCreatePost}>+ Create a post</button>
            </div>
          )}

          {showCreatePost && (
            <CreatePost
              onPostCreated={handlePostCreated}
              onCancel={() => setShowCreatePost(false)}
            />
          )}

          {error && (
            <div className="error-message">
              {error}
            </div>
          )}

          {posts.length === 0 && !loading ? (
            <div className="post">
              <p>No posts found</p>
            </div>
          ) : (
            posts.map((post) => (
              <Post
                key={post.ID}
                post={post}
                onPostUpdate={handlePostUpdate}
              />
            ))
          )}

          {loading && (
            <div className="loading">Loading more posts...</div>
          )}
        </main>

        <ChatWebSocket username={user.nickname || user.username} />
      </div>
    </>
  );
}
