import { useState } from 'react';
import { formatDate } from '../utils/helpers';
import { useComments, useReactions } from '../hooks';

const Post = ({ post, onPostUpdate }) => {
  const [showComments, setShowComments] = useState(false);
  const [commentText, setCommentText] = useState('');
  const { comments, sendComment, toggleComments } = useComments();
  const { toggleReaction } = useReactions();

  const handleLikeToggle = async (reactionType) => {
    const result = await toggleReaction('post', post.ID, reactionType);
    if (result.success) {
      // Update post data with new reaction counts
      onPostUpdate(post.ID, result.data);
    }
  };

  const handleCommentToggle = () => {
    setShowComments(!showComments);
    toggleComments(post.ID);
  };

  const handleCommentSubmit = async (e) => {
    e.preventDefault();
    if (!commentText.trim()) return;

    const result = await sendComment(commentText, post.ID);
    if (result.success) {
      setCommentText('');
      // Optionally update comment count in parent
    }
  };

  const handleCommentLike = async (commentId, reactionType) => {
    await toggleReaction('comment', commentId, reactionType);
  };

  return (
    <div className="post" postid={post.ID}>
      <div className="post-header">
        <div className="user-info">
          <span className="material-icons profile-icon">account_circle</span>
          <span className="username">{post.Username}</span>
          <span className="timestamp">{formatDate(post.CreatedAt)}</span>
        </div>
      </div>

      <div className="post-content">
        <h3 className="post-title">{post.Title}</h3>
        <p className="post-text">{post.Content}</p>
        
        {post.Categories && post.Categories.length > 0 && (
          <div className="post-categories">
            {post.Categories.map((category, index) => (
              <span key={index} className="category-tag">{category}</span>
            ))}
          </div>
        )}
      </div>

      <div className="post-actions">
        <div className="reaction-buttons">
          <div 
            className={`like-button ${post.Have === 'like' ? 'active' : ''}`}
            onClick={() => handleLikeToggle('like')}
          >
            <span 
              id="like" 
              data-type="post" 
              data-status={post.Have === 'like' ? 'on' : 'of'} 
              data-id={post.ID}
              className="material-icons"
            >
              thumb_up_off_alt
            </span>
            <b>{post.Like || 0}</b>
          </div>

          <div 
            className={`like-button ${post.Have === 'dislike' ? 'active' : ''}`}
            onClick={() => handleLikeToggle('dislike')}
          >
            <span 
              id="dislike" 
              data-type="post" 
              data-status={post.Have === 'dislike' ? 'on' : 'of'} 
              data-id={post.ID}
              className="material-icons"
            >
              thumb_down_off_alt
            </span>
            <b>{post.DisLike || 0}</b>
          </div>

          <div className="comment-toggle" onClick={handleCommentToggle}>
            <span 
              id="comment" 
              className={showComments ? 'on' : 'of'}
              posteid={post.ID}
            >
              {post.Comments || 0} ðŸ’¬
            </span>
          </div>
        </div>
      </div>

      {showComments && (
        <div className="comments-section">
          {comments[post.ID] && comments[post.ID].map((comment) => (
            <div key={comment.ID} className="comment-content">
              <div className="user-name">{comment.Username}</div>
              <p className="comment-text">{comment.Content}</p>
              <div className="comment-actions">
                <div 
                  className={`like-button ${comment.Have === 'like' ? 'active' : ''}`}
                  onClick={() => handleCommentLike(comment.ID, 'like')}
                >
                  <span 
                    id="like" 
                    data-type="comment" 
                    data-status={comment.Have === 'like' ? 'on' : 'of'} 
                    data-id={comment.ID}
                    className="material-icons"
                  >
                    thumb_up_off_alt
                  </span>
                  <b>{comment.Like || 0}</b>
                </div>
                <div 
                  className={`like-button ${comment.Have === 'dislike' ? 'active' : ''}`}
                  onClick={() => handleCommentLike(comment.ID, 'dislike')}
                >
                  <span 
                    id="dislike" 
                    data-type="comment" 
                    data-status={comment.Have === 'dislike' ? 'on' : 'of'} 
                    data-id={comment.ID}
                    className="material-icons"
                  >
                    thumb_down_off_alt
                  </span>
                  <b>{comment.DisLike || 0}</b>
                </div>
                <span className="timestamp">{formatDate(comment.CreatedAt)}</span>
              </div>
            </div>
          ))}

          <div className="comment-input-section">
            <form onSubmit={handleCommentSubmit}>
              <div className="input-wrapper">
                <textarea
                  className="comment-input"
                  data-idpost={post.ID}
                  value={commentText}
                  onChange={(e) => setCommentText(e.target.value)}
                  placeholder="Write a comment..."
                  rows="2"
                />
                <button type="submit" className="send-button">
                  Send
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
};

export default Post;
