import { useState } from 'react';
import { validateCategories } from '../utils/helpers';

const Sidebar = ({ username, onCategoryChange }) => {
  const [selectedCategory, setSelectedCategory] = useState('all');

  const categories = [
    { value: 'all', label: 'All' },
    { value: 'Tech Support', label: 'Tech Support' },
    { value: 'General Discussion', label: 'General Discussion' },
    { value: 'Tutorials', label: 'Tutorials' },
    { value: 'Gaming', label: 'Gaming' },
    { value: 'Hobbies & Interests', label: 'Hobbies & Interests' },
    { value: 'Job Listings', label: 'Job Listings' },
    { value: 'Announcements', label: 'Announcements' }
  ];

  const handleCategoryClick = (category) => {
    setSelectedCategory(category);
    onCategoryChange(category);
  };

  return (
    <aside className="sidebar">
      <div className="contact">
        <span className="material-icons">account_circle</span>
        <span>{username}</span>
        <span className="online-indicator"></span>
      </div>
      
      <h3>Category</h3>
      <div className="category-list">
        {categories.map((category) => (
          <button
            key={category.value}
            className={`cat ${selectedCategory === category.value ? 'active' : ''}`}
            value={category.value}
            onClick={() => handleCategoryClick(category.value)}
          >
            {category.label}
          </button>
        ))}
      </div>
    </aside>
  );
};

export default Sidebar;
