import { useState, useEffect, useRef } from 'react';

const ChatWebSocket = ({ username }) => {
  const [socket, setSocket] = useState(null);
  const [users, setUsers] = useState([]);
  const [onlineUsers, setOnlineUsers] = useState([]);
  const [selectedUser, setSelectedUser] = useState(null);
  const [messages, setMessages] = useState({});
  const [messageInput, setMessageInput] = useState('');
  const [showChat, setShowChat] = useState(false);
  const [notifications, setNotifications] = useState([]);
  const socketRef = useRef(null);

  useEffect(() => {
    if (username) {
      connectWebSocket();
    }

    return () => {
      if (socketRef.current) {
        socketRef.current.close();
      }
    };
  }, [username]);

  const connectWebSocket = () => {
    const ws = new WebSocket(`ws://localhost:8080/ws`);
    socketRef.current = ws;
    setSocket(ws);

    ws.onopen = () => {
      console.log('WebSocket connected');
    };

    ws.onmessage = (event) => {
      const data = JSON.parse(event.data);
      handleWebSocketMessage(data);
    };

    ws.onclose = () => {
      console.log('WebSocket disconnected');
      // Attempt to reconnect after 3 seconds
      setTimeout(() => {
        if (username) {
          connectWebSocket();
        }
      }, 3000);
    };

    ws.onerror = (error) => {
      console.error('WebSocket error:', error);
    };
  };

  const handleWebSocketMessage = (data) => {
    if (data.type === "users") {
      data.users.forEach(user => {
        if (user.username === username) {
          setUsers(user.allUsers || []);
        }
      });
    } else if (data.type === "online-users") {
      setOnlineUsers(data.users || []);
    } else if (data.content === "is-typing" || data.content === "no-typing") {
      // Handle typing indicators
      console.log(`${data.sender} ${data.content}`);
    } else {
      // Regular message
      const message = data;
      
      if (message.receiver === username) {
        // Show notification
        showNotification(message.sender, 'You have a new message');
        
        // Add message to messages state
        setMessages(prev => ({
          ...prev,
          [message.sender]: [
            ...(prev[message.sender] || []),
            {
              ...message,
              timestamp: new Date().toISOString()
            }
          ]
        }));
      }
    }
  };

  const showNotification = (sender, text) => {
    const notification = {
      id: Date.now(),
      sender,
      text
    };
    
    setNotifications(prev => [...prev, notification]);
    
    // Remove notification after 3 seconds
    setTimeout(() => {
      setNotifications(prev => prev.filter(n => n.id !== notification.id));
    }, 3000);
  };

  const sendMessage = () => {
    if (!messageInput.trim() || !selectedUser || !socket) return;

    const message = {
      sender: username,
      receiver: selectedUser,
      content: messageInput,
      timestamp: new Date().toISOString()
    };

    socket.send(JSON.stringify(message));
    
    // Add message to local state
    setMessages(prev => ({
      ...prev,
      [selectedUser]: [
        ...(prev[selectedUser] || []),
        message
      ]
    }));

    setMessageInput('');
  };

  const selectUser = (user) => {
    setSelectedUser(user);
    setShowChat(true);
  };

  const isUserOnline = (user) => {
    return onlineUsers.some(onlineUser => onlineUser.username === user);
  };

  return (
    <>
      {/* Notifications */}
      {notifications.map(notification => (
        <div key={notification.id} className="pop">
          <h3>{username}</h3>
          <p>You have a new message from {notification.sender}</p>
        </div>
      ))}

      {/* Contacts sidebar */}
      <aside className="contacts">
        <div style={{ marginBottom: '1rem' }}>
          <span 
            className="material-icons" 
            id="cancel"
            onClick={() => setShowChat(false)}
          >
            close
          </span>
          <h3>Contacts</h3>
        </div>
        
        <div 
          id="contact-list"
          style={{
            height: `${window.innerHeight / 4}px`,
            overflowY: 'auto',
            border: '3px solid rgb(226, 226, 226)',
            padding: '15px',
            borderRadius: '20px'
          }}
        >
          {users.map((user) => (
            <div 
              key={user}
              className={`contact-item ${selectedUser === user ? 'active' : ''}`}
              onClick={() => selectUser(user)}
            >
              <span className="material-icons">account_circle</span>
              <span className="contact-name">{user}</span>
              {isUserOnline(user) && (
                <span className="online-indicator active"></span>
              )}
            </div>
          ))}
        </div>

        {/* Chat window */}
        {showChat && selectedUser && (
          <div className="chat-window">
            <div className="chat-header">
              <h4>Chat with {selectedUser}</h4>
              <span 
                className="material-icons close-chat"
                onClick={() => setShowChat(false)}
              >
                close
              </span>
            </div>
            
            <div className="chat-messages">
              {(messages[selectedUser] || []).map((message, index) => (
                <div 
                  key={index}
                  className={`message ${message.sender === username ? 'sent' : 'received'}`}
                >
                  <div className="message-content">{message.content}</div>
                  <div className="message-time">
                    {new Date(message.timestamp).toLocaleTimeString()}
                  </div>
                </div>
              ))}
            </div>
            
            <div className="chat-input">
              <input
                type="text"
                value={messageInput}
                onChange={(e) => setMessageInput(e.target.value)}
                onKeyPress={(e) => e.key === 'Enter' && sendMessage()}
                placeholder="Type a message..."
              />
              <button onClick={sendMessage}>Send</button>
            </div>
          </div>
        )}
      </aside>
    </>
  );
};

export default ChatWebSocket;
